/*checkbox*/
.check-container  /* Customize the label (the container) */ {
	width: fit-content;
	display: block;
	font-weight: 400;
	position: relative;
	padding-left: 20px;
	margin-left: 15px;
	margin-bottom: 4px;
	cursor: pointer;
	font-size: 12px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none; }

.check-container input  /* Hide the browser's default checkbox */ {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0; }


.checkmark  /* Create a custom checkbox */ {
	position: absolute;
	top: -1px;
	left: 0;
	height: 15px;
	width: 15px;
	background-color: none;
	border: 1px solid rgb(178,178,178); }


/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
	content: "";
	position: absolute;
	display: none; }

.check-container input:checked ~ .checkmark:after {
	display: block; }


/* Style the checkmark/indicator */
.check-container .checkmark:after {
	left: 4px;
	top: 0px;
	width: 3px;
	height: 8px;
	border: solid rgb(40,40,40);
	border-width: 0 2px 2px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg); }
