@import '../_App.sass';
$sideMenu-width: 200px;
$accountForm-width: 700px;

.borderRight {
	border-right: 4px solid rgb(142,211,95); }

.my-account-titles {
	font-size: 1.2em;
	margin-bottom: 30px; }

.account_sideMenu_container {
	display: table;
	margin: 10px auto 0px;
	width: fit-content;
	padding-right: $sideMenu-width;
	position: relative;
	@include myAccountFirstBreak {
		position: absolute; }
	#newUserAccountMsg {
		width: 700px;
		white-space: normal;
		padding: 20px 50px;
		background-color: white;
		margin: 0 0 10px 0;
		border: 1px solid $std-container-borderColour;
		border-radius: 5px;
		font-size: 0.9em;
		text-align: center; }
	// @include accountSecondBreak
	// 	width: 100vw
	// 	border-left: none
	// 	border-right: none
	.accountContent {
		display: inline-block;
		vertical-align: top;
		width: $accountForm-width;
		white-space: normal;
		@include myAccountFirstBreak {
			position: absolute; }
		#cardContainer {
			width: 700px;
			display: grid;
			margin-top: 10px 0 0 0;
			grid-template-columns: repeat(auto-fit, 229px);
			grid-auto-rows: 290px;
			justify-content: left;
			grid-gap: 5px;
			// @include accountSecondBreak
			// 	justify-content: center
			.browse-card {
				height: 290px;
				width: 230px;
				box-shadow: 1px 1px 2px rgb(35,35,35);
				font-size: 0.92em;
				margin: 0;
				display: block;
				margin: 0 auto; } } } }

#loading-spacer {
	min-width: $accountForm-width;
	display: inline-block;
	vertical-align: top;
	#msg-spacer {
		background-color: white;
		text-align: center;
		padding: 20px 10px;
		font-size: 0.9em;
		letter-spacing: 0.05em;
		border-radius: 5px;
		border: 1px solid $std-container-borderColour; } }


.sideMenu {
	display: inline-block;
	width: 300px;
	background-color: white;
	width: $sideMenu-width;
	border: 1px solid rgb(226, 230, 233);
	border-radius: 5px;
	margin: 0 10px 0 0;
	background-color: white;
	font-family: 'Lato', sans-serif;
	.sideMenuOption {
		height: 55px;
		padding: 17px 0 0 20px;
		display: block;
		font-size: 0.95em;
		font-family: 'Lato', sans-serif;
		color: black;
		&:last-child {
			border-radius: 0 0 5px 5px; }
		&:first-child {
			border-radius: 5px 0 0 0; }
		i {
			float: right;
			margin: 7px 25px 0 0; } }
	.selectedTab {
		border-left: 4px solid rgb(55,69,79); } }


