
#contactForm {
	border: 1px solid grey;
	border-radius: 5px;
	background-color: white;
	position: fixed;
	bottom: 20px;
	right: 20px;
	height: 300px;
	width: 500px;
	padding: 20px;
	textarea {
		width: 100%;
		height: 220px;
		margin-top: 20px;
		border: 1px solid grey; } }

#authName {
	font-size: 0.8em; }

#emailBtn {
	position: absolute;
	top: 15px;
	right: 20px; }

#closeMailBtn {
	position: absolute;
	top: -30px;
	left: 0;
	background-color: Transparent;
	border: none;
	cursor: pointer; }

