@import '../_App.sass';




$indexGrid-titleHeight: 40px;

.index-grid {
	width: 100vw;
	display: grid;
	height: $card-height + 50px;
	grid-template-columns: repeat(auto-fill, $card-width);
	grid-auto-rows: min-content;
	grid-gap: 20px;
	justify-content: center;
	padding: $indexGrid-titleHeight 10px 0 10px;
	white-space: nowrap;
	overflow: hidden;
	@include featureFirstBreak {
		display: block;
		overflow-x: scroll; } }

.browse-card {
	&:not(:last-of-type) {
		margin-right: 20px; } }

// Hide scrollbar for Chrome, Safari and Opera
.index-grid::-webkit-scrollbar {
	display: none; }

// Hide scrollbar for IE, Edge and Firefox
.index-grid {
	-ms-overflow-style: none /* IE and Edge */;
	scrollbar-width: none  /* Firefox */;


