

// App sass //

$defaultcolor: rgb(243,245,247);
$recipe-form-color: #555555;
$placeholder-color: #999999;
$std-btn-border-radius: 3px;
$std-btn-border-fontSize: 0.8em;
$std-pageHeight: 1000px;
$std-input-field-border-radius: 2px;
$logoGreen: rgb(141, 211, 95);
$std-label-color: grey;


// STD CARD
$std-card-box-shadow: 2px 2px 4px rgb(160,160,160);
$browse-card-border-radius: 4px;
$card-height: 320px;
$card-width: 250px;

// STD CONTAINER
$std-container-box-shadow: 3px 3px 2px 2px rgb(50,50,50);
$std-container-border-radius: 15px;
$std-container-borderColour: rgb(226,230,233);
$std-pageMinHeight: 650px;


$header-height: 80px;

$overlayBackground: rgba(190,190,190, 0.4);

// error message font font-family:
$error-font: 'Lato', sans-serif;

$moreWidth: 160px;
$left-ban-padding: 20px;


