@import '../_App.sass';


$profile-height: 120px;


body {
	background-color: blue; }


#cookBody {
	padding-top: 30px; }

#profile-container {
	margin: 0 auto;
	width: fit-content;
	#img-wrapper {
		height: $profile-height;
		width: $profile-height;
		display: inline-block;
		@include authPageFirstBreak {
			display: none; }
		img {
			display: inline-block;
			height: 100%;
			width: 100%;
			object-fit: cover;
			border-radius: 3px; } }
	#profile-info-container {
		display: inline-block;
		vertical-align: top;
		position: relative;
		width: 400px;
		#authName {
			// position: relative
			// // top: 20px
			// left: 10px
			padding-top: 20px;
			padding-left: 20px;
			font-size: 1.1em; }
		#authAbout {
			// position: relative
			// // top: 50px
			// left: 10px
			padding-left: 20px;
			padding-top: 10px;
			font-size: 0.9em;
			font-size: 0.8em; } } }


#divider {
	border-top: 1px solid rgb(212, 212, 212);
	width: 50vw;
	margin: 30px auto; }




